<template>
  <div>
    <bread-nav :nav="nav" />
    <main-part>
      <el-tabs v-model="curTabIdx"
               @tab-click="onTabChange">
        <el-tab-pane label="年级科目设置">
          <grade ref="grade"/>
        </el-tab-pane>
        <el-tab-pane label="知识点设置">
          <knowledge ref="knowledge"/>
        </el-tab-pane>
      </el-tabs>
    </main-part>
  </div>
</template>

<script>
  import Grade from './Grade'
  import Knowledge from './Knowledge'
  export default {
    components: {
      Grade, Knowledge
    },
    data () {
      return {
        nav: [{name: '题库管理', path: 'go(-1)'}, {name: '基础设置', path: ''}],
        curTabIdx: '0',
      }
    },
    methods: {
      async onTabChange () {
        let grade = this.$refs.grade
        let knowledge = this.$refs.knowledge
        if (this.curTabIdx === '0') {
          await grade.queryGradeList()
          await grade.querySubjectList()
        } else {
          this.$search(knowledge.table)
        }
      }
    }
  }
</script>

