<template>
  <div>
    <table class="table">
      <tr>
        <th width="20%">年级</th>
        <th width="20%">科目</th>
        <th width="40%">知识点</th>
        <th width="20%">操作</th>
      </tr>
      <tr v-for="item in table.list">
        <td>{{item.gradeName || '--'}}</td>
        <td>{{item.subjectName || '--'}}</td>
        <td>{{item.knowledgeName || '--'}}</td>
        <td>
          <span class="oper" @click="doSetting(item)">设置知识点</span></td>
        <td>
          <!--<span @click="goToExam(item)" class="oper">查看考生</span>-->
          <!--<span class="oper">监考视频</span>-->
        </td>
      </tr>
    </table>
    <paging :data.sync="table"/>
    <el-dialog title="设置知识点"
               :visible.sync="dialogFormVisible"
               width="500px"
               center>
      <el-form label-width="90px"
               class="form">
        <el-form-item label="年级">
          <el-input v-model="knowledge.gradeName"
                    disabled/>
        </el-form-item>
        <el-form-item label="科目">
          <el-input v-model="knowledge.subjectName"
                    disabled/>
        </el-form-item>
        <el-form-item label="知识点"
                      class="fwb"/>

        <el-form-item v-for="(item, idx) in knowledgeArr"
                      :key="idx"
                      :label="'知识点' + (idx + 1)">
          <el-input v-model="item.knowledgeName"/>
          <el-button icon="el-icon-delete"
                     class="btn-delete"
                     v-if="idx !== 0"
                     :loading="item.deleteing"
                     @click="doDeleteKnowledge(item, idx)"/>
        </el-form-item>

        <el-form-item label="新增知识点"
                      class="oper"
                      @click.native="doAddKnowledge"/>

        <el-form-item>
          <el-button type="primary"
                     :loading="submitting"
                     @click="doSubmit">提交
          </el-button>
          <!--<el-button class="btn-submit"-->
                     <!--@click="doAddKnowledge">新增知识点-->
          <!--</el-button>-->
          <el-button @click="dialogFormVisible = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        table: {
          url: '/qKnowledge/queryQKnowledgeNameManagePage.do',
          el: '.table',
          query: {
            pageNo: 1,
            pageSize: 10
          },
          list: [],
          total: 0,
          noData: true
        },
        dialogFormVisible: false,
        knowledge: {},
        knowledgeArr: [],
        submitting: false
      }
    },
    // computed: {
    //   knowledgeArr () {
    //     if (!this.knowledge.knowledgeName) return ['']
    //     return this.knowledge.knowledgeName.split(',')
    //   }
    // },
    created () {
      this.$search(this.table)
    },
    methods: {
      async doSetting (item) {
        this.dialogFormVisible = true
        this.knowledge = item
        this.knowledgeArr = []
        console.log(this.knowledgeArr)
        let knowledgeArr = await this.$api.queryQKnowledgeInfoList({
          gradeId: this.knowledge.gradeId,
          subjectId: this.knowledge.subjectId
        })
        if (!knowledgeArr.length) {
          knowledgeArr = [{
            knowledgeName: null
          }]
        }
        knowledgeArr.forEach(item => {
          item.deleteing = false
        })
        this.knowledgeArr = knowledgeArr
      },
      doAddKnowledge () {
        // this.knowledge.knowledgeName += ','
        this.knowledgeArr.push({})
      },
      async doDeleteKnowledge (item, idx) {
        // 新增了没保存的情况
        if (!item.id) {
          this.knowledgeArr.splice(idx, 1)
          this.$remind('知识点删除成功')
          return
        }

        item.deleteing = true
        try {
          await this.$api.delQKnowledgeInfo({id: item.id})
          this.knowledgeArr.splice(idx, 1)
          this.$remind('知识点删除成功')
        } catch (e) {}
        item.deleteing = false
        // try {
        //   await this.$api.delQKnowledgeInfo()
        // } catch (e) {}
      },
      // async onInputBlur (e, item) {
      //   if (item.id) {
      //     await this.$api.updateQKnowledgeInfo({
      //       id: item.id,
      //       knowledgeName: item.knowledgeName
      //     })
      //     this.$remind('更新成功')
      //   }
      // },
      async doSubmit () {
        this.submitting = true
        let count = 0
        this.knowledgeArr.forEach(async item => {
          // 更新操作
          if (item.id) {
            try {
              await this.$api.updateQKnowledgeInfo({
                id: item.id,
                knowledgeName: item.knowledgeName
              })
            } catch (e) {}
          } else {
            try {
              // 新增操作
              await this.$api.addQKnowledgeInfo({
                gradeId: this.knowledge.gradeId,
                subjectId: this.knowledge.subjectId,
                knowledgeName: item.knowledgeName
              })
            } catch (e) {}
          }
          count++
          if (count === this.knowledgeArr.length) {
            this.submitting = false
            this.$remind('知识点保存成功')
            this.dialogFormVisible = false
            this.$search(this.table)
          }
        })
      }
    }
  }
</script>

<style lang='sass' scoped>
  .fwb
    font-weight: bold
  .oper /deep/ label
    color: $main-c
    cursor: pointer

  .btn-submit
    line-height: 1
    color: #fff
    background-color: $assist-c
    border-color: $assist-c

  .form
    .el-input
      width: 80%
    .btn-delete
      width: 15%
      margin-left: 5%
      color: $error-c
</style>
