<template>
  <div class="clearfix">
    <el-card class="card card-grade">
      <el-form class="form">
        <el-form-item v-for="(item, idx) in gradeList"
                      :key="idx"
                      :label="idx === 0 ? '年级' : ''"
                      label-width="100px">
          <el-input v-model="item.gradeName"
                    v-show="item.active"
                    :ref="'gradeInput' + idx"
                    @blur="onGradeInputBlur($event, item)"/>
          <el-button class="btn-name"
                     v-show="!item.active"
                     :class="{'btn--active': item.id === curGradeId}"
                     :loading="item.loading"
                     @click="querySubjectList(item)">{{item.gradeName}}
          </el-button>
          <el-button icon="el-icon-edit"
                     class="btn-edit"
                     :loading="item.editing"
                     @click="doEditGrade(item, idx)"/>
          <el-button icon="el-icon-delete"
                     class="btn-delete"
                     :loading="item.deleteing"
                     @click="doDeleteGrade(item, idx)"/>
        </el-form-item>
        <el-form-item label-width="100px">
          <el-button class="btn-add"
                     type="primary"
                     @click="doAddGrade">增加年级
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <div class="card-transfer">
      <span class="el-icon-d-arrow-right"></span>
    </div>

    <el-card class="card card-knowledge">
      <el-form class="form">
        <el-form-item v-for="(item, idx) in subjectList"
                      :key="idx"
                      :label="idx === 0 ? '科目' : ''"
                      label-width="100px">
          <el-input v-model="item.subjectName"
                    :ref="'gradeInput' + idx"
                    v-show="item.active"
                    @blur="onSubjectInputBlur($event, item)"/>
          <el-button class="btn-name"
                     v-show="!item.active">
            {{item.subjectName}}
          </el-button>
          <el-button icon="el-icon-edit"
                     class="btn-edit"
                     :loading="item.editing"
                     @click="doEditSubject(item)"/>
          <el-button icon="el-icon-delete"
                     class="btn-delete"
                     :loading="item.deleteing"
                     @click="doDeleteSubject(item, idx)"/>
        </el-form-item>
        <el-form-item label-width="100px">
          <el-button class="btn-add"
                     type="primary"
                     @click="doAddSubject">增加科目
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        gradeList: [],
        curGradeId: null,
        subjectList: []
      }
    },
    computed: {
      activeGrade () {
        return this.gradeList.find(item => item.active)
      },
      activeSubject () {
        return this.subjectList.find(item => item.active)
      }
    },
    async created () {
      await this.queryGradeList()
      await this.querySubjectList()
    },
    methods: {
      async queryGradeList () {
        try {
          let gradeList = await this.$api.queryQGradeList()
          gradeList.forEach(item => {
            item.active = false
            item.loading = false
            item.deleteing = false
            item.editing = false
          })
          this.curGradeId = gradeList[0].id
          this.gradeList = gradeList
        } catch (e) {}
      },
      doAddGrade () {
        if (this.activeGrade) {
          this.activeGrade.active = false
        }
        this.gradeList.push({
          gradeName: '',
          active: true
        })
      },
      async doDeleteGrade (item, idx) {
        if (item.deleteing) return
        try {
          await this.$confirm('确认删除该年级?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          if (!item.id) {
            this.gradeList.splice(idx, 1)
            return
          }
          item.deleteing = true
          try {
            await this.$api.delQGrade({id: item.id})
            this.gradeList.splice(this.gradeList.findIndex(item => item.id === item.id), 1)
            this.$remind('年级删除成功')
          } catch (e) {}
          item.deleteing = false
        } catch (e) {}
      },
      async doEditGrade (item, idx) {
        if (this.activeGrade) {
          this.activeGrade.active = false
        }
        // console.log()
        // TODO BUG: 聚焦事件不生效
        this.$refs['gradeInput' + idx][0].focus()
        // console.log(this.$refs['gradeInput' + idx])
        item.active = true
      },
      async onGradeInputBlur (e, item) {
        if (item.editing) return
        // 更新操作
        if (item.id) {
          item.active = false
          item.editing = true
          try {
            await this.$api.updateQGrade({
              id: item.id,
              gradeName: item.gradeName
            })
            this.$remind('年级更新成功')
          } catch (e) {}
          item.editing = false
          return
        }

        try {
          item.id = await this.$api.addQGrade({gradeName: item.gradeName})
          this.$remind('年级新增成功')
        } catch (e) {}
      },
      async querySubjectList (item) {
        // if (!item || !item.id) return
        let loading = this.$loading({
          target: '.card-knowledge',
          text: '科目数据加载中...'
        })
        let timer = null
        if (item) {
          this.curGradeId = item.id
          timer = setTimeout(() => {
            item.loading = true
          }, 500)
        }

        let subjectList = await this.$api.queryQSubjectList({
          gradeId: this.curGradeId
        })
        subjectList.forEach(item => {
          item.active = false
          item.deleteing = false
          item.editing = false
        })
        clearTimeout(timer)
        if (item) item.loading = false
        this.subjectList = subjectList
        loading.close()
      },
      async onSubjectInputBlur (e, item) {
        if (item.editing) return
        if (!item.id) {
          try {
            item.id = await this.$api.addQSubject({
              gradeId: this.curGradeId,
              subjectName: item.subjectName
            })
            item.active = false
            this.$remind('科目新增成功')
          } catch (e) {}
          return
        }
        try {
          item.editing = true
          await this.$api.updateQSubject({
            id: item.id,
            gradeId: this.curGradeId,
            subjectName: item.subjectName
          })
          item.active = false
          item.editing = false
          this.$remind('科目更新成功')
        } catch (e) {}
      },
      doEditSubject (item) {
        item.active = true
      },
      async doDeleteSubject (item, idx) {
        try {
          await this.$confirm('确认删除该科目?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          if (!item.id) {
            this.subjectList.splice(idx, 1)
            return
          }
          item.deleteing = true
          try {
            await this.$api.delQSubject({id: item.id})
            this.subjectList.splice(this.subjectList.findIndex(item => item.id === item.id), 1)
            this.$remind('科目删除成功')
          } catch (e) {}
          item.deleteing = false
        } catch (e) {}
      },
      doAddSubject () {
        if (!this.curGradeId) {
          this.$message({
            message: '请先选择一个年级',
            type: 'warning'
          })
          return
        }
        if (this.activeSubject) this.activeSubject.active = false
        this.subjectList.push({
          subjectName: null,
          active: true
        })
      }
    }
  }
</script>

<style lang='sass' scoped>
  .card
    $h: calc(100vh - 185px)
    float: left
    width: 45%
    height: $h
    padding: 0
    overflow: auto
    &-transfer
      float: left
      width: 10%
      text-align: center
      .el-icon-d-arrow-right
        font-weight: bold
        font-size: 30px
        line-height: $h
        color: $main-c

  .form
    width: 500px
    margin: 0
    .el-input, .btn-name
      width: 60%
    .btn-name
      overflow: hidden
      white-space: nowrap
      text-overflow: ellipsis
    .btn-delete, .btn-edit
      width: 15%
      margin-left: 5%
      color: $error-c
    .btn-add
      width: 60%
    .btn--active
      color: $main-c
      border-color: #b4e8fa
      background-color: #e6f7fd
</style>
